.ColorPicker {
    position: absolute;
    top: -20px;
    left: 0;
    width: 100px;
    height: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.color {
    width: 18px;
    height: 18px;
    border-radius: 50%;
}

.color:hover {
    cursor: pointer;
}